<script>
import HouseDetail from '@/components/HouseDetail/index.vue'
import { mapState } from 'vuex' // 

export default {
  name: 'ExchangeSquare',
  components: { HouseDetail },
  data () {
    return {
      activeTab: 'houseList', // tab
      houseList: [], // APIVuex
      selectedHouse: null, // dialog
      showPostDialog: false, // 
      userInfo: {} // 
    }
  },
  computed: {
    ...mapState(['houseDetail'])
  },
  mounted () {
    this.userInfo = {
      name: '',
      posts: [], // 
      comments: [], // 
      likes: [], // 
      favorites: [] // 
    }
  },
  methods: {
    openCommentDialog (house) {
      this.selectedHouse = house
      // 
    },
    submitComment () {
      // 
      this.$notify.success({
        title: '',
        message: '',
        duration: 2000,
        position: 'top-right',
        offset: 80
      })
    },
    submitPost () {
      // 
      this.$notify.success({
        title: '',
        message: '',
        duration: 2000,
        position: 'top-right',
        offset: 80
      })
      this.showPostDialog = false
    }
  }
}
</script>

<template>
  <div class="community-vitality">
    <el-tabs v-model="activeTab" type="card" class="main-tabs">
      <el-tab-pane label="" name="houseList">
        <div class="house-list">
          <el-input placeholder="..." class="search-input"/>
          <div v-if="this.houseDetail.length > 0" class="main-content">
            <HouseDetail
              v-for="(item, index) in this.houseDetail"
              :key="index"
              :feature="item"
            />
          </div>
        </div>
      </el-tab-pane>

      <el-tab-pane label="" name="posts">
        <div class="posts-section">
          <el-button type="primary" @click="showPostDialog = true"></el-button>
          <div class="posts-list">
            <el-card v-for="i in 5" :key="i" class="post-card">
              <div class="post-title"> {{ i }}</div>
              <div class="post-content">... </div>
              <div class="post-actions">
                <el-button size="mini"></el-button>
                <el-button size="mini"></el-button>
              </div>
            </el-card>
          </div>
        </div>

        <el-dialog v-model="showPostDialog" title="">
          <el-form>
            <el-input placeholder=""/>
            <el-input type="textarea" placeholder="" rows="5"/>
            <el-tag></el-tag>
          </el-form>
          <el-button type="primary" @click="submitPost"></el-button>
        </el-dialog>
      </el-tab-pane>

      <el-tab-pane label="" name="account">
        <div class="account-section">
          <el-card class="user-info-card">
            <h3></h3>
            <p>: {{ userInfo.name }}</p>
            <p>: </p>
            <el-button size="small"></el-button>
          </el-card>

          <el-card class="user-activity">
            <h3></h3>
            <ul>
              <li v-for="i in 3" :key="i"> {{ i }}</li>
            </ul>
          </el-card>

          <el-card class="user-activity">
            <h3></h3>
            <ul>
              <li v-for="i in 3" :key="i"> {{ i }}</li>
            </ul>
          </el-card>

          <el-card class="user-activity">
            <h3></h3>
            <ul>
              <li v-for="i in 3" :key="i"> {{ i }}</li>
            </ul>
          </el-card>

          <el-card class="user-activity">
            <h3></h3>
            <ul>
              <li v-for="i in 3" :key="i"> {{ i }}</li>
            </ul>
          </el-card>
        </div>
      </el-tab-pane>
    </el-tabs>

    <el-dialog v-model="selectedHouse" :title="selectedHouse ? selectedHouse.title + ' ' : ''" width="60%">
      <div class="comment-section">
        <el-card class="publisher-self">
          <h4></h4>
          <p></p>
        </el-card>

        <el-card class="user-comments">
          <h4></h4>
          <div v-for="i in 4" :key="i" class="comment-item">
            <p> {{ i }}</p>
            <el-rate disabled :value="3.5"/>
            <el-button size="mini"></el-button>
          </div>
          <el-input type="textarea" placeholder="..." rows="3"/>
          <el-button type="primary" @click="submitComment"></el-button>
        </el-card>
      </div>
    </el-dialog>
  </div>
</template>

<style scoped lang="less">
.community-vitality {
  width: 100%;
  min-height: 100vh;
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 20px;
  box-sizing: border-box;
  overflow-y: auto;
}

.house-list,
.posts-section,
.account-section {
  padding: 10px;
}

.search-input {
  margin-bottom: 15px;
}

.main-content {
  width: 100%;
  background-color: #fff;
  overflow-y: auto;
  border-radius: 0 0 10px 10px;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: #f8fafc;
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: #cbd5e1;
    border-radius: 3px;
    transition: background 0.3s ease;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #94a3b8;
  }
}

.post-card,
.user-info-card,
.user-activity {
  margin-bottom: 15px;
  box-shadow: 0 4px 12px rgba(255, 208, 75, 0.3);
  transition: box-shadow 0.3s ease;

  &:hover {
    box-shadow: 0 6px 16px rgba(255, 208, 75, 0.4);
  }

  h3, h4 {
    color: #2c3e50;
    font-size: 16px;
    margin-bottom: 10px;
  }
}

.post-actions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.comment-section {
  display: flex;
  flex-direction: column;
  gap: 20px;

  .comment-item {
    border-bottom: 1px solid #f0f0f0;
    padding: 10px 0;
  }
}

.el-button[type="primary"] {
  background: #ffd04b;
  border-color: #ffd04b;
  color: #2c3e50;

  &:hover {
    background: #ffbc40;
    border-color: #ffbc40;
  }
}

h3, h4 {
  color: #2c3e50;
}

@media (max-width: 768px) {
  .community-vitality {
    padding: 10px;
  }

  .house-list,
  .posts-section,
  .account-section {
    padding: 5px;
  }
}
</style>
