<script>
import { mapState } from 'vuex'

export default {
  name: 'PropertyComparison',
  data () {
    return {
      selectedHouses: [],
      compareData: [],
      viewTab: 'table',
      charts: []
    }
  },
  mounted () {
    this.$nextTick(() => {
      if (this.compareData.length === 0 && this.wishList.length > 0) {
        this.selectedHouses = this.wishList.map(h => h.id)
        this.addToCompare(true) // 
        this.viewTab = 'chart'
        // this.$message.success(` ${this.wishList.length} `)
        this.$notify.success({
          title: '',
          message: ` ${this.wishList.length} `,
          duration: 2000,
          position: 'top-right',
          offset: 80
        })
      }
      if (this.compareData.length > 0) {
        this.initAllCharts()
      }
    })
  },
  beforeDestroy () {
    this.clearCharts()
  },
  watch: {
    compareData: {
      handler (newVal) {
        if (newVal.length > 0) {
          this.$nextTick(() => {
            this.clearCharts()
            this.initAllCharts()
          })
        } else {
          this.clearCharts()
        }
      },
      deep: true
    },
    viewTab (newVal) {
      if (newVal === 'chart') {
        this.$nextTick(() => {
          this.charts.forEach(chart => chart && chart.resize())
        })
      }
    },
    '$route' (to, from) {
      if (to.name === 'PropertyComparison' || to.path.includes('comparison')) {
        this.$nextTick(() => {
          setTimeout(() => {
            this.charts.forEach(chart => chart && chart.resize())
          }, 100)
        })
      }
    }
  },
  computed: {
    ...mapState(['wishList']),
    houseOptions () {
      return this.wishList.map(house => ({
        value: house.id,
        label: house.title || ` ${house.id}`
      }))
    }
  },
  methods: {
    addToCompare (silent = false) {
      if (this.selectedHouses.length === 0) {
        // this.$message.warning('')
        this.$notify.warning({
          title: '',
          message: '',
          duration: 2000,
          position: 'top-right',
          offset: 80
        })
        return
      }
      let addedCount = 0
      this.selectedHouses.forEach(id => {
        const alreadyExists = this.compareData.some(item => item.id === id)
        if (alreadyExists) return
        const house = this.wishList.find(h => h.id === id)
        if (house) {
          const roomInfo = house.room || ''
          const roomParts = roomInfo.split('|')
          const layout = roomParts[0] ? roomParts[0].trim() : ''
          const area = roomParts[1] ? roomParts[1].trim() : ''
          const floorInfo = roomParts[2] ? roomParts[2].trim() : ''
          this.compareData.push({
            id: house.id,
            title: house.title || '',
            price: house.price ? `${house.price} ${house.unit || '/'}` : '',
            layout: layout,
            area: area,
            floor: floorInfo || '',
            building: house.building || '',
            address: house.address || '',
            description: house.description || '',
            contact: house.contact || '',
            tag: house.tag || '',
            pic_link: house.pic_link || ''
          })
          addedCount++
        }
      })
      if (!silent) {
        if (addedCount > 0) {
          // this.$message.success(` ${addedCount} `)
          this.$notify.success({
            title: '',
            message: ` ${addedCount} `,
            duration: 2000,
            position: 'top-right',
            offset: 80
          })
        } else {
          // this.$message.info('')
          this.$notify.info({
            title: '',
            message: '',
            duration: 2000,
            position: 'top-right',
            offset: 80
          })
        }
      }
      if (addedCount === 0) {
        this.$notify.info({
          title: '',
          message: '',
          duration: 2000,
          position: 'top-right',
          offset: 80
        })
      }
      this.selectedHouses = []
    },
    removeItem (index) {
      this.compareData.splice(index, 1)
      // this.$message.info('')
      this.$notify.info({
        title: '',
        message: '',
        duration: 2000,
        position: 'top-right',
        offset: 80
      })
    },
    clearCompare () {
      this.compareData = []
      this.selectedHouses = []
      // this.$message.info('')
      this.$notify.info({
        title: '',
        message: '',
        duration: 2000,
        position: 'top-right',
        offset: 80
      })
    },
    exportReport () {
      this.$message.info(' PDF/Excel ...')
    },
    saveCompare () {
      this.$message.info('...')
    },
    clearCharts () {
      if (this.resizeHandler) {
        window.removeEventListener('resize', this.resizeHandler)
        this.resizeHandler = null
      }
      this.charts.forEach(chart => chart && chart.dispose && chart.dispose())
      this.charts = []
    },
    initAllCharts () {
      if (typeof echarts === 'undefined') {
        console.warn('ECharts 500ms ...')
        setTimeout(() => this.initAllCharts(), 500)
        return
      }
      this.clearCharts()
      this.initScatterChart()
      this.initRadarChart()
      this.initBarChart()
      this.resizeHandler = () => {
        this.charts.forEach(chart => chart && chart.resize())
      }
      window.removeEventListener('resize', this.resizeHandler)
      window.addEventListener('resize', this.resizeHandler)
    },
    extractArea (areaStr) {
      const match = areaStr.match(/[\d.]+/)
      return match ? parseFloat(match[0]) : 0
    },
    extractPrice (priceStr) {
      const match = priceStr.match(/[\d,]+/)
      return match ? parseFloat(match[0].replace(/,/g, '')) : 0
    },
    // 1. 
    initScatterChart () {
      const chartDom = this.$refs.scatterChart
      if (!chartDom || this.compareData.length === 0) return
      const myChart = echarts.init(chartDom)
      this.charts.push(myChart)
      const prices = this.compareData.map(h => this.extractPrice(h.price))
      const areas = this.compareData.map(h => this.extractArea(h.area))
      const maxPrice = Math.max(...prices) || 1000 // 
      const maxArea = Math.max(...areas) || 50
      //  +1000 +50
      const yAxisMax = Math.ceil((maxPrice + 1000) / 1000) * 1000
      const xAxisMax = Math.ceil((maxArea + 10) / 20) * 20
      const data = this.compareData.map(house => {
        const area = this.extractArea(house.area)
        const price = this.extractPrice(house.price)
        const unitPrice = area > 0 ? (price / area).toFixed(1) : '0'
        return {
          name: house.title,
          value: [area, price],
          unitPrice,
          itemStyle: {
            color: price < 4000 ? '#67c23a' : price < 7000 ? '#e6a23c' : '#f56c6c',
            borderColor: '#fff',
            borderWidth: 3,
            shadowBlur: 10,
            shadowColor: 'rgba(0,0,0,0.3)'
          }
        }
      })
      const option = {
        title: {
          text: '',
          left: 'center',
          top: 10,
          textStyle: { fontSize: 17, fontWeight: 'bold' }
        },
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(0,0,0,0.85)',
          borderWidth: 0,
          padding: [10, 15],
          textStyle: { color: '#fff', fontSize: 14 },
          position: function (pos, params, dom, rect, size) {
            const x = pos[0]
            const viewWidth = size.viewSize[0]
            const tipWidth = 260
            return [
              Math.min(x + 10, viewWidth - tipWidth - 10), // 
              pos[1] - 150 // 
            ]
          },
          formatter: params => {
            const d = params.data
            return `
          <div style="line-height:1.6">
            <strong style="font-size:15px">${d.name}</strong><br/>
            <strong>${d.value[0]} </strong><br/>
            <strong style="color:#f56c6c">${d.value[1]} </strong><br/>
            <strong style="color:#e6a23c">${d.unitPrice} /</strong>
          </div>
        `
          }
        },
        grid: { top: 80, left: 70, right: 70, bottom: 70 },
        xAxis: {
          name: '',
          type: 'value',
          min: 0,
          max: xAxisMax,
          nameLocation: 'middle',
          nameGap: 30,
          nameTextStyle: { fontWeight: 'bold' },
          axisLabel: { fontSize: 12 }
        },
        yAxis: {
          name: '',
          type: 'value',
          min: 0,
          max: yAxisMax,
          nameLocation: 'middle',
          nameGap: 50,
          nameTextStyle: { fontWeight: 'bold' },
          axisLabel: { fontSize: 12 }
        },
        series: [
          {
            type: 'scatter',
            data: data,
            symbolSize: 24,
            emphasis: { scale: 1.8 }
          },
          {
            name: '80/',
            type: 'line',
            data: [[0, 0], [xAxisMax, xAxisMax * 80]],
            symbol: 'none',
            lineStyle: { color: '#e74c3c', width: 2.5, type: 'dashed' },
            tooltip: { show: false },
            markLine: {
              silent: false,
              label: { show: true, position: 'end', formatter: '80/ ' },
              lineStyle: { color: '#e74c3c', type: 'dashed', width: 2 }
            }
          }
        ]
      }
      myChart.setOption(option)
    },
    // 2. 
    initRadarChart () {
      const chartDom = this.$refs.radarChart
      if (!chartDom || this.compareData.length === 0) return
      const myChart = echarts.init(chartDom)
      this.charts.push(myChart)
      const indicator = [
        { name: '', max: 100 },
        { name: '', max: 100 },
        { name: '', max: 100 },
        { name: '', max: 100 },
        { name: '', max: 100 }
      ]
      const seriesData = this.compareData.map(house => {
        const area = this.extractArea(house.area)
        const price = this.extractPrice(house.price)
        const areaScore = Math.min(100, area)
        const priceScore = Math.max(20, 100 - price / 100)
        const floorScore = /|/.test(house.floor) ? 95 : //.test(house.floor) ? 70 : 50
        const trafficScore = /||/.test(house.address + house.tag) ? 95 : 65
        const decoScore = /|||/.test(house.description + house.tag) ? 95 : 65
        return {
          name: house.title.length > 7 ? house.title.slice(0, 7) + '' : house.title,
          value: [priceScore, areaScore, floorScore, trafficScore, decoScore].map(Math.round)
        }
      })
      const option = {
        title: {
          text: '',
          left: 'center',
          top: 8,
          textStyle: { fontSize: 17, fontWeight: 'bold' }
        },
        tooltip: {
          trigger: 'item',
          formatter: params => {
            return `<strong>${params.name}</strong><br/>
                ${params.value[0]}<br/>
                ${params.value[1]}<br/>
                ${params.value[2]}<br/>
                ${params.value[3]}<br/>
                ${params.value[4]}`
          }
        },
        radar: {
          indicator,
          radius: '62%',
          name: { textStyle: { color: '#333', fontSize: 13 } },
          splitNumber: 5
        },
        series: [{
          type: 'radar',
          data: seriesData,
          symbol: 'circle',
          symbolSize: 8,
          areaStyle: { opacity: 0.15 },
          lineStyle: { width: 3 },
          emphasis: {
            areaStyle: { opacity: 0.3 }
          }
        }]
      }
      myChart.setOption(option)
    },
    // 3. 
    initBarChart () {
      const chartDom = this.$refs.barChart
      if (!chartDom) return
      const myChart = echarts.init(chartDom)
      this.charts.push(myChart)
      const names = this.compareData.map(h => h.title.length > 10 ? h.title.substr(0, 10) + '...' : h.title)
      const prices = this.compareData.map(h => this.extractPrice(h.price))
      const areas = this.compareData.map(h => this.extractArea(h.area))
      const option = {
        title: { text: '', left: 'center', top: 10 },
        tooltip: { trigger: 'axis' },
        legend: { top: 30 },
        grid: { top: 80, bottom: 70, left: 60, right: 80 },
        xAxis: {
          type: 'category',
          data: names,
          axisLabel: { interval: 0, rotate: 25 }
        },
        yAxis: [
          {
            type: 'value',
            name: ' ()',
            position: 'left',
            axisLine: { show: true, lineStyle: { color: '#ff7675' } }
          },
          {
            type: 'value',
            name: ' ()',
            position: 'right',
            axisLine: { show: true, lineStyle: { color: '#74b9ff' } }
          }
        ],
        series: [
          {
            name: '',
            type: 'bar',
            data: prices,
            itemStyle: { color: '#ff7675' },
            barWidth: '20%'
          },
          {
            name: '',
            type: 'bar',
            yAxisIndex: 1,
            data: areas,
            itemStyle: { color: '#74b9ff' },
            barWidth: '20%'
          }
        ]
      }
      myChart.setOption(option)
    }
  }
}
</script>

<template>
  <div class="property-comparison">
    <div class="selection-area">
      <el-select
        v-model="selectedHouses"
        multiple
        filterable
        clearable
        placeholder=""
        style="flex: 1;"
      >
        <el-option
          v-for="item in houseOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
      <el-button type="primary" @click="addToCompare"></el-button>
      <el-button @click="clearCompare"></el-button>
    </div>
    <!--  -->
    <el-tabs v-model="viewTab" class="compare-tabs">
      <!--  -->
      <el-tab-pane label="" name="table">
        <el-table
          v-if="compareData.length"
          :data="compareData"
          border
          stripe
          style="width: 100%; margin-top: 10px;"
        >
          <el-table-column prop="title" label="" width="300" fixed/>
          <el-table-column prop="price" label="" width="120"/>
          <el-table-column prop="layout" label="" width="120"/>
          <el-table-column prop="area" label="" width="100"/>
          <el-table-column prop="floor" label="" width="140"/>
          <el-table-column prop="building" label="" width="180"/>
          <el-table-column prop="address" label="" min-width="220"/>
          <el-table-column prop="tag" label="" width="100"/>
          <el-table-column label="" width="100" fixed="right">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click="removeItem(scope.$index)">
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-empty v-else description=""/>
      </el-tab-pane>
      <!--  -->
      <el-tab-pane label="" name="card">
        <div class="card-view" v-if="compareData.length">
          <el-card
            v-for="(house, index) in compareData"
            :key="house.id"
            class="compare-card"
          >
            <div class="card-header">
              <img v-if="house.pic_link" :src="house.pic_link" alt="" class="house-img"/>
              <div class="tag" v-if="house.tag">{{ house.tag }}</div>
            </div>
            <h3>{{ house.title }}</h3>
            <p><strong></strong>{{ house.price }}</p>
            <p><strong></strong>{{ house.layout }}</p>
            <p><strong></strong>{{ house.area }}</p>
            <p><strong></strong>{{ house.floor }}</p>
            <p><strong></strong>{{ house.building }}</p>
            <p><strong></strong>{{ house.address }}</p>
            <p><strong></strong>{{ house.description }}</p>
            <el-button size="small" type="danger" @click="removeItem(index)" style="margin-top: 10px;">
              
            </el-button>
          </el-card>
        </div>
        <el-empty v-else description=""/>
      </el-tab-pane>
      <!--  -->
      <el-tab-pane label="" name="chart">
        <div class="chart-wrapper" v-if="compareData.length === 0">
          <el-empty
            description=""
            :image-size="200"
            style="padding: 80px 0;">
            <p style="color: #999; margin-top: 20px;">
              
            </p>
          </el-empty>
        </div>
        <div class="chart-wrapper" v-else>
          <el-row :gutter="20" style="margin: -10px;">
            <!--  -->
            <el-col :span="24" :lg="12" style="margin-bottom: 20px;">
              <el-card shadow="hover" class="chart-card">
                <div slot="header" class="card-title">
                  <span> vs </span>
                </div>
                <div ref="scatterChart" class="echart"></div>
              </el-card>
            </el-col>
            <!--  -->
            <el-col :span="24" :lg="12" style="margin-bottom: 20px;">
              <el-card shadow="hover" class="chart-card">
                <div slot="header" class="card-title">
                  <span></span>
                </div>
                <div ref="radarChart" class="echart"></div>
                <div style="padding: 12px 16px; color: #666; font-size: 13px; line-height: 1.6; background: #f9f9f9; border-radius: 8px;">
                  =100-/100=100
                </div>
              </el-card>
            </el-col>
            <!--  -->
            <el-col :span="24">
              <el-card shadow="hover" class="chart-card">
                <div slot="header" class="card-title">
                  <span></span>
                </div>
                <div ref="barChart" class="echart" style="height: 500px;"></div>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </el-tab-pane>
    </el-tabs>
    <!--  -->
    <div class="actions" v-if="compareData.length">
      <el-button type="primary" @click="exportReport"></el-button>
      <el-button @click="saveCompare"></el-button>
    </div>
  </div>
</template>

<style scoped lang="less">
.property-comparison {
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  box-sizing: border-box;
  overflow-y: auto;
}

.selection-area {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 12px rgba(255, 208, 75, 0.3);
}

.compare-tabs {
  .card-view {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding: 20px 0;

    .compare-card {
      width: calc(33.33% - 20px);
      min-width: 320px;
      box-shadow: 0 4px 12px rgba(255, 208, 75, 0.3);
      transition: all 0.3s ease;

      &:hover {
        transform: translateY(-4px);
        box-shadow: 0 8px 20px rgba(255, 208, 75, 0.4);
      }

      .card-header {
        position: relative;

        .house-img {
          width: 100%;
          height: 180px;
          object-fit: cover;
          border-radius: 8px 8px 0 0;
        }

        .tag {
          position: absolute;
          top: 10px;
          left: 10px;
          background: #42b883;
          color: #fff;
          padding: 4px 10px;
          border-radius: 20px;
          font-size: 12px;
          font-weight: bold;
        }
      }

      h3 {
        color: #2c3e50;
        margin: 15px 0 10px;
        font-size: 16px;
      }

      p {
        margin: 6px 0;
        color: #555;
        font-size: 14px;
      }
    }
  }

  .chart-wrapper {
    padding: 10px 0;

    .chart-card {
      height: 100%;
      min-height: 586px;
      border-radius: 12px;
      overflow: hidden;

      .card-title {
        font-weight: bold;
        font-size: 16px;
        color: #2c3e50;
        padding: 0 10px;
      }

      .echart {
        width: 100%;
        height: 420px !important;
      }
    }
  }
}

.actions {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
  margin-top: 20px;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 12px rgba(255, 208, 75, 0.3);
}

.el-button[type="primary"] {
  background: #ffd04b;
  border-color: #ffd04b;
  color: #2c3e50;

  &:hover {
    background: #ffbc40;
    border-color: #ffbc40;
  }
}

@media (max-width: 768px) {
  .selection-area {
    flex-direction: column;
    align-items: stretch;
  }

  .card-view .compare-card {
    width: 100%;
    min-width: unset;
  }
}
</style>
