<script>
import { mapState, mapMutations } from 'vuex' //  Vuex  wishList state  mutations

export default {
  name: 'WishList',
  computed: {
    ...mapState(['wishList']) //  Vuex  wishList 
  },
  methods: {
    ...mapMutations(['removeFromWishList']), //  mutation 
    locateHouse (house) {
      // emit  Vuex action
      this.$emit('locate', house) //  parent 
    },
    contactPerson (contact) {
      // e.g., open dialog or tel: link
      alert(`: ${contact}`) // 
    },
    removeHouse (id) {
      this.removeFromWishList(id) //  mutation 
    }
  }
}
</script>

<template>
  <div class="wish-list" @click.stop>
    <h3 class="title"></h3>
    <el-empty v-if="!wishList.length" description=""/>
    <el-table v-else :data="wishList" border stripe>
      <el-table-column prop="title" label="" width="180"/>
      <el-table-column prop="contact" label="" width="120"/>
      <el-table-column prop="price" label=" (/)"/>
      <el-table-column prop="room" label=" ()"/>
      <el-table-column label="" width="110" align="center">
        <template #default="scope">
          <div class="action-buttons">
            <el-button
              class="action-btn locate-btn"
              size="small" icon="el-icon-location"
              @click="locateHouse(scope.row)"
            ></el-button>
            <el-button
              class="action-btn contact-btn"
              size="small"
              icon="el-icon-phone"
              @click="contactPerson(scope.row.contact)"
            ></el-button>
            <el-button
              class="action-btn remove-btn"
              size="small"
              icon="el-icon-delete"
              @click="removeHouse(scope.row.id)"
            ></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<style scoped lang="less">
.wish-list {
  position: absolute;
  left: 0;
  top: 0;
  width: 60%;
  height: 100%;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-right: 1px solid #e8e8e8;
  box-shadow: 2px 0 12px rgba(0, 0, 0, 0.08);
  z-index: 2;
  padding: 20px;
  box-sizing: border-box;
  overflow-y: auto;

  .title {
    color: #2c3e50;
    font-size: 18px;
    text-align: center;
    margin-bottom: 20px;
  }

  .el-table {
    .action-buttons {
      display: flex;
      flex-direction: column;
      gap: 8px;
      padding: 8px 0;

      :deep(.el-button + .el-button) {
        margin-left: 0 !important;
      }
    }

    .action-buttons .action-btn {
      height: 32px;
      padding: 0 12px;
      font-size: 13px;
      font-weight: 500;
      border: none;
      border-radius: 16px;
      color: #2c3e50;
      background: #ffd04b;
      box-shadow: 0 2px 6px rgba(255, 208, 75, 0.4);
      transition: all 0.2s ease;

      &:hover {
        background: #ffc218;
        transform: translateY(-1px);
        box-shadow: 0 4px 10px rgba(255, 208, 75, 0.6);
      }
    }
  }
}
</style>
